/* General styles for all menus */
@media only screen and (max-width:767px) {
  /*.container > .navbar-header {
        margin-left: 0;
    }*/
  /*.container > .cbp-spmenu {
        margin-left: 0;
    }*/
  .cbp-spmenu {
    background: #fff;
    position: fixed;
    border-right: #999999 1px solid;
  }
  .cbp-spmenu::-webkit-scrollbar {
    width: 6px;
  }
  .cbp-spmenu 7::-webkit-scrollbar-track {
    background-color: #eaeaea;
    border-left: 1px solid #ccc;
  }
  .cbp-spmenu::-webkit-scrollbar-thumb {
    background-color: #ccc;
  }
  .cbp-spmenu::-webkit-scrollbar-thumb:hover {
    background-color: #aaa;
  }
  .cbp-spmenu a {
    display: block;
  }
  .cbp-spmenu a:hover {
    background: #cdcccc;
  }
  /* Orientation-dependent styles for the content of the menu */
  .cbp-spmenu-vertical {
    width: 240px;
    height: 100%;
    top: 0;
    z-index: 1000;
    overflow: auto;
  }
  .cbp-spmenu-vertical a {
    padding: 1em;
  }
  .cbp-spmenu-horizontal {
    width: 100%;
    height: 150px;
    left: 0;
    z-index: 1000;
    overflow: hidden;
  }
  .cbp-spmenu-horizontal h3 {
    height: 100%;
    width: 20%;
    float: left;
  }
  .cbp-spmenu-horizontal a {
    float: left;
    width: 20%;
    padding: 0.8em;
    border-left: 1px solid #258ecd;
  }
  /* Vertical menu that slides from the left or right */
  .cbp-spmenu-left {
    left: -240px;
  }
  .cbp-spmenu-right {
    right: -240px;
  }
  .cbp-spmenu-left.menu-open {
    left: 0px;
  }
  .cbp-spmenu-right.menu-open {
    right: 0px;
  }
  /* Horizontal menu that slides from the top or bottom */
  .cbp-spmenu-top {
    top: -150px;
  }
  .cbp-spmenu-bottom {
    bottom: -150px;
  }
  .cbp-spmenu-top.menu-open {
    top: 0px;
  }
  .cbp-spmenu-bottom.menu-open {
    bottom: 0px;
  }
  /* Push classes applied to the body */
  .push-body {
    overflow-x: hidden;
    position: relative;
    left: 0;
  }
  .push-body-toright {
    left: 240px;
  }
  .push-body-toleft {
    left: -240px;
  }
  /* Transitions */
  .cbp-spmenu,
  .push-body {
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    transition: all 0.3s ease;
  }
  /* Example media queries */
}
@media only screen and (max-width:767px) and screen and (max-width: 55.1875em) {
  .cbp-spmenu-horizontal {
    font-size: 75%;
    height: 110px;
  }
  .cbp-spmenu-top {
    top: -110px;
  }
  .cbp-spmenu-bottom {
    bottom: -110px;
  }
}
@media only screen and (max-width:767px) and screen and (max-height: 26.375em) {
  .cbp-spmenu-vertical {
    font-size: 90%;
    width: 190px;
  }
  .cbp-spmenu-left,
  .push-body-toleft {
    left: -190px;
  }
  .cbp-spmenu-right {
    right: -190px;
  }
  .push-body-toright {
    left: 190px;
  }
}
/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}
.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}
.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir="rtl"] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}
